html {
	box-sizing: border-box;
	height: 100%;
	background-color: #fafafa;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
::selection {
	background: pink;
}
a {
	color: white;
	font-family: 'Source Sans Pro',  monospace;
	font-variant: none;
	text-decoration: none;
}
body {
	margin: 0;
	padding: 0;
	border: 0;
}
body.m-locked {
	overflow: hidden;
}
body:before {
	display: block;
	width: 100%;
	height: 100%;
}
p,
h6 {
	font-family: 'Source Sans Pro',  monospace;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
figure,
h1,
h4 {
	font-family: 'Source Sans Pro',  monospace;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
h4.title {
  letter-spacing: 0.7;
  font-weight: 400;
  text-align: left;
  letter-spacing: 0.7px;
  color: black;
  padding-top: 100px;
  margin-bottom: 0;
	text-transform: uppercase;
}

h1.m-textSmall, h1.m-textLarge {
	font-family: 'Source Sans Pro',  monospace;
	color: black;
	font-weight: 200;
}

h1.m-textSmall {
font-family: 'Source Sans Pro',  monospace;
}

h2,
h3,
h5 {
	font-family: 'Source Sans Pro',  monospace;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
h1 {
	text-align: left;
	font-weight: 300;
	line-height: 1.3;
	color: $darkGreyText;
}
h1.brandDescription {
	font-weight: 300;
}
h2 {
	font-size: $fontUp;
	font-weight: 300;
	line-height: 36px;
}
h3 {
	font-size: $fontUp;
	font-weight: 400;
	line-height: 24px;
}
h4 {
	font-size: $fontDown;
	font-weight: 400;
	letter-spacing: 0.1px;
	color: $darkGreyText;
}
h5 {
	display: inline-block;
	font-size: 14px;
	letter-spacing: 0.7px;
	font-weight: 500;
	padding-right: 22px;
	text-transform: uppercase;
	color: black;
text-align: left;
	  margin-bottom: 0;
	}
p, ul>li {
	font-size: $fontBase;
	font-weight: 400;
	letter-spacing: 0.2px;
	color: #525252;
	line-height: 1.7;
}
h4.m-projTitle {
